@charset "UTF-8";

.header-site-title {
  font-size: 1.5rem;
  width: 100%;
}

@media screen and (max-width: 480px) {
  .header-site-title {
    font-size: 0.95rem;
  }
}

.header-color {
  background: radial-gradient(circle 200px at center, #666666 0%, #333333 50%, #000000 100%);
  width: 100%;
  justify-content: center;
}

.header {
  width: 100%;
  justify-content: center;
}

/* pcの際のメニューバー */

@media screen and (min-width: 701px) {
  .hamburger-menu {
    width: 100%;
  }

  .menu-content {
    width: 100%;
  }

  .menu-content ul {
    justify-content: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }

  .menu-content li {
    width: 14%;
    text-align: center;
    float: left;
    background-color: #1b2538;
    border-right: 1px solid #bbbbbb;
  }

  .menu-content li:last-child {
    border-right: none;
  }

  .menu-content li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }

  .menu-content li a:hover:not(.active) {
    background-color: #a9bce2;
  }

  .menu-content .active {
    background-color: #da3c41;
  }
}

/* スマホの際のメニューバー */

@media screen and (max-width: 700px) {
  .menu-btn {
    position: fixed;
    top: 10px;
    right: 10px;
    display: flex;
    height: 50px;
    width: 60px;
    justify-content: center;
    align-items: center;
    z-index: 90;
    background-color: #000000;
  }

  .menu-btn span {
    content: "";
    display: block;
    height: 3px;
    width: 25px;
    border-radius: 3px;
    background-color: #ffffff;
    position: absolute;
  }

  .menu-btn span:before {
    content: "";
    display: block;
    height: 3px;
    width: 25px;
    border-radius: 3px;
    background-color: #ffffff;
    position: absolute;
    bottom: 8px;
  }

  .menu-btn span:after {
    content: "";
    display: block;
    height: 3px;
    width: 25px;
    border-radius: 3px;
    background-color: #ffffff;
    position: absolute;
    top: 8px;
  }

  .menu-content {
    width: 70%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 100%;
    /*leftの値を変更してメニューを画面外へ*/
    z-index: 80;
    background-color: #000000;
    transition: all 0.5s;
    /*アニメーション設定*/
  }

  .menu-content ul {
    padding: 70px 10px 0;
  }

  .menu-content ul li {
    border-bottom: solid 1px #ffffff;
    list-style: none;
  }

  .menu-content ul li a {
    display: block;
    width: 80%;
    font-size: 15px;
    box-sizing: border-box;
    color: #ffffff;
    text-decoration: none;
    padding: 9px 15px 10px 0;
    position: relative;
  }

  .menu-content ul li a::before {
    content: "";
    width: 7px;
    height: 7px;
    border-top: solid 2px #ffffff;
    border-right: solid 2px #ffffff;
    transform: rotate(45deg);
    position: absolute;
    right: 11px;
    top: 16px;
  }
}

#menu-btn-check {
  display: none;
}

#menu-btn-check:checked ~ .menu-content {
  left: 30%;
  /*メニューを画面内へ*/
}

#menu-btn-check:checked ~ .menu-btn span {
  background-color: rgba(255, 255, 255, 0);
  /*メニューオープン時は真ん中の線を透明にする*/
}

#menu-btn-check:checked ~ .menu-btn span::before {
  bottom: 0;
  transform: rotate(45deg);
  background-color: #ffffff;
}

#menu-btn-check:checked ~ .menu-btn span::after {
  top: 0;
  transform: rotate(-45deg);
  background-color: #ffffff;
}

@media (max-width: 700px) {
  .site-title {
    font-size: 0.95rem;
  }
}

